.App {
  padding: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 14px;
}

.App_loading{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

.TempBox {
    background: #441240;
    -webkit-transition: all .2s ease-in;
    -o-transition: all .2s ease-in;
    transition: all .2s ease-in;
    display: inline-block;
    padding-top: 2rem;
    padding-bottom: 2rem;
    text-align: center;
    color: black;
    position: relative;
    font-size: 2rem;
    font-weight: 500;
    border-radius: 4px;
    cursor: pointer;

    @include place-at-center();

    width: 120px;

    // 반응형 디자인
    @media screen and (min-width:320px) and (max_width:667px) {
      width: 1024px;
    }

    @media screen and (min-width:320px) and (max_width:667px) and (orientation: portrait) {
      width: 768px;
    }

    @media("<medium") {
      width: 90%;
    }

    // 마우스 상태에 따라 다른 효과 지정
    &:hover {
      background: $oc-green-6;
    }
    &:active {
      margin-top: 3px;
      background: $oc-green-8;
    }
}

